<template>
  <v-row justify="center">
    <v-expansion-panels accordion focusable class="pa-2">
      <v-expansion-panel v-for="(item, i) in 5" :key="i" class="white--text">
        <v-expansion-panel-header v-if="wait">
          <v-col cols="3" sm="3"
            ><h5>{{ convertDate[i] }}</h5></v-col
          >
          <v-col cols="5" sm="5" class="pa-0">
            <v-list-item class="pa-1">
              <v-img
                max-width="35"
                max-height="18"
                v-bind:src="
                  'https://openweathermap.org/img/w/' + get.icons[i] + '.png'
                "
              >
              </v-img>
              <v-list-item class="pl-1">
                <v-list-item-subtitle class="white--text">
                  <h5>{{ get.min[i] }}° / {{ get.max[i] }}°</h5>
                </v-list-item-subtitle>
              </v-list-item>
            </v-list-item>
          </v-col>
          <v-col cols="4" sm="4" class="pl-6">
            <span style="font-size:0.7rem">{{ get.Descriptions[i] }}</span>
          </v-col>
        </v-expansion-panel-header>
        <v-expansion-panel-content>
          <v-list-item class="pa-0" two-line>
            <v-list-item-subtitle class="white--text pa-0">
              <v-list-item-content>
                <span>
                  <v-icon color="primary">mdi-thermometer</v-icon>
                  อุณหภูมิทั่วไป: {{ get.temp[i] }} °C
                </span>
              </v-list-item-content>
              <v-list-item-content>
                <span>
                  <v-icon color="primary">mdi-water-percent</v-icon>
                  ความชื้นอากาศ: {{ get.humidity[i] }} %
                </span>
              </v-list-item-content>
              <v-list-item-content>
                <span>
                  <v-icon color="primary">mdi-coolant-temperature</v-icon>
                  ให้ความรู้สึกเหมือน {{ get.feels_like[i] }} °C
                </span>
              </v-list-item-content>
              <v-list-item-content>
                <span>
                  <v-icon color="primary">mdi-weather-windy</v-icon> ความเร็วลม
                  {{ get.wind[i] }} m/s
                </span>
              </v-list-item-content>
            </v-list-item-subtitle>
          </v-list-item>
        </v-expansion-panel-content>
      </v-expansion-panel>
    </v-expansion-panels>
  </v-row>
</template>
<script>
import axios from "axios";
export default {
  methods: {},
  data() {
    return {
      Data: [],
      day: [],
      days: [],
      convertDate: [],
      wait: false,
      get: {
        humidity: [],
        feels_like: [],
        wind: [],
        temp: [],
        icons: [],
        min: [],
        max: [],
        Descriptions: [],
      },
    };
  },
  async created() {
    await axios
      .get(
        "https://api.openweathermap.org/data/2.5/forecast?q=surin&lang=th&appid=393143e567ce80bd9eea5386d8c06ac6&units=metric"
      )
      .then((response) => {
        console.log(response.data.list);
        for (let i = 0; i < response.data.list.length; i = i + 8) {
          this.Data.push(response.data.list[i].dt_txt);
          this.get.min.push(response.data.list[i].main.temp_min);
          this.get.max.push(response.data.list[i].main.temp_max);
          this.get.icons.push(response.data.list[i].weather[0].icon);
          this.get.Descriptions.push(response.data.list[i].weather[0].description);
          this.get.humidity.push(response.data.list[i].main.humidity);
          this.get.feels_like.push(response.data.list[i].main.feels_like);
          this.get.wind.push(response.data.list[i].wind.speed);
          this.get.temp.push(response.data.list[i].main.temp);
        }
        for (let i = 0; i < 5; i++) {
          // console.log(this.Data[i]);
          const date = new Date(this.Data[i]); // create a new reference here
          this.day = date.toLocaleString("th-TH", {
            weekday: "long",
            // day: "numeric",
            // month: "long",
          });
          this.days.push(this.day);
          this.convertDate[i] = this.days[i].replace("วัน", "");
        }
        console.log(this.convertDate);
        this.wait = true;
      });
  },
};
</script>
<style>
.sub {
  color: #7d0ca0 !important;
  align-items: center !important;
  align-self: center !important;
  align-content: center !important;
  display: flex;
}
.head {
  color: #b2fefa !important;
}
.v-expansion-panel::before {
  box-shadow: none !important;
}
.v-expansion-panel-header {
  padding: 2px !important;
  /* padding: 0px; */
}
.theme--light.v-expansion-panels .v-expansion-panel {
  background: no-repeat !important;
  /* background: rgba(131, 241, 131, 0.466); */
}
