<template>
  <div id="chart">
    <apexchart
      type="line"
      height="350"
      ref="chart"
      :options="chartOptions"
      :series="series"
    ></apexchart>
  </div>
</template>
<script>
var lastDate = 0;
var data = [];
var TICKINTERVAL = 10000;
let XAXISRANGE = 36000;
const timestamps = new Date(); // create a new reference here

export default {
  data() {
    return {
    day:[],
    days:[],
      series: [
        {
          data: data.slice(),
        },
      ],
      chartOptions: {
        chart: {
          id: "realtime",
          height: 350,
          type: "line",
          animations: {
            enabled: true,
            easing: "linear",
            dynamicAnimation: {
              speed: 1000,
            },
          },
          toolbar: {
            show: false,
          },
          zoom: {
            enabled: false,
          },
        },
        dataLabels: {
          enabled: false,
        },
        stroke: {
          curve: "smooth",
        },
        title: {
          text: "Dynamic Updating Chart",
          align: "left",
        },
        markers: {
          size: 0,
        },
        xaxis: {
          type: "datetime",
          range: XAXISRANGE,
        },
        yaxis: {
          max: 100,
        },
        legend: {
          show: false,
        },
      },
    };
  },
  created() {
    this.getDayWiseTimeSeries(timestamps.getTime(), 10, {
      min: 10,
      max: 90,
    });
    console.log(timestamps.getTime());
  },
  methods: {
    getDayWiseTimeSeries(baseval, count, yrange) {
      var i = 0;
      while (i < count) {
        var x = baseval;
        var y =
          Math.floor(Math.random() * (yrange.max - yrange.min + 1)) +
          yrange.min;

        data.push({
          x,
          y,
        });
        lastDate = baseval;
        baseval += TICKINTERVAL;
        i++;
      }
    },
    getNewSeries(baseval, yrange) {
      var newDate = baseval + TICKINTERVAL;
      lastDate = newDate;

      for (var i = 0; i < data.length - 10; i++) {
        data[i].x = newDate - XAXISRANGE - TICKINTERVAL;
        data[i].y = 0;
      }

      data.push({
        x: newDate,
        y:
          Math.floor(Math.random() * (yrange.max - yrange.min + 1)) +
          yrange.min,
      });
    },
    resetData() {
      data = data.slice(data.length - 10, data.length);
    },
  },
  mounted() {
    setInterval(() => {
      this.getNewSeries(lastDate, {
        min: 10,
        max: 90,
      });

      this.$refs.chart.updateSeries([
        {
          data: data,
        },
      ]);
    }, 5000);
    setInterval(() => {
      this.resetData();

      this.$refs.chart.updateSeries(
        [
          {
            data,
          },
        ],
        false,
        true
      );
    }, 60000);
  },
};
</script>
